import { Component, OnInit,OnDestroy } from '@angular/core'
import { RemoteDataSource} from "../../../@core/utils/remoteDataSource"
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { CompleterService, CompleterData } from 'ng2-completer';
import { Http } from '@angular/http';
@Component({
    selector: 'common-user-gateway',
    template: `
    <nb-card>
    <nb-card-header>
      {{head}}
      <button  class="btn btn-primary btn-icon btn-tn right-btn" type="button">
      导出<i  class="fa fa-external-link"></i>
    </button>
    </nb-card-header>
  
    <nb-card-body>
      <ng2-smart-table [settings]="settings" [source]="source" (deleteConfirm)="onDeleteConfirm($event)">
      </ng2-smart-table>
    </nb-card-body>
  </nb-card>
    `
    ,
    styles: [
        `
        nb-card {
            transform: translate3d(0, 0, 0);
          }
          .right-btn {
              float:right
          }
          .add-text{
            font-size: 14px;
          }
        `
    ]


})
export class GatewayComponent implements OnInit, OnDestroy{
    head = "网关管理"
    settings = {
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            inputClass: "add-input"
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
            confirmDelete: true,
        },
        columns: {
            eqp_id: {
                title: '设备号',
                type: 'string',
            },
              pos:{
                title: '位置',
                type:'string'
            },
            install_time:{
                title: '设备日期',
                type:'string'
            },
            status:{
                title: '状态',
                type:'string',
                editable: false
            },
            fixTimes:{
                title: "运维次数",
                type: 'string'
            },
            action: {
                title:'更多操作',
                type:'html',
                valuePrepareFunction:(cell,row)=>{
                  return `<a class="btn btn-primary btn-sm" href="#/pages/common-user/equipment/gateway/${row.id}">详情</a>`
                },
                filter:false,
                editor:false, 
                editable:false,     
            }
       },
        actions: false,
        setPaging: {
            page: 20,
            perPage: 10
        },
        setPage: {
            page: 20,
            perPage: 10
        },
    }
    source: RemoteDataSource;
    constructor(private service: SmartTableService, http: Http) {
        const data = this.service.getData();
        this.source = new RemoteDataSource(http, { endPoint: '/api' });

    }
    ngOnInit(){
   }
   onDeleteConfirm(event): void {
    if (window.confirm('确定删除吗？')) {
        event.confirm.resolve();
    } else {
        event.confirm.reject();
    }
   }
  ngOnDestroy() {
  }
}


