import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {CommonUserComponent} from "./common-user-component"
import { IndexComponent} from "./index/index.component"
import { TrafficChartComponent} from "./index/watch.charts"
import { MapComponent } from "./map/map.component"
import {GatewayComponent} from "./equipment/gateway.component"
import {SmokeComponent} from "./equipment/smoke.component"
import { Smoke } from '../../@core/data/models';
import {GatewayDetailComponent} from './equipment/gateway.detail'
import {PicComponent} from './pic/pic.component'
const routes: Routes = [{
  path: '',
  component: CommonUserComponent,
  children: [
  {
    path: 'index',
    component: IndexComponent,
  },
  {
    path: 'map',
    component: MapComponent,
  },
  {
    path: 'equipment/gateway',
    component: GatewayComponent,
  },
  {
    path: 'equipment/gateway/:id',
    component: GatewayDetailComponent,
  },
  {
    path: 'equipment/smoke',
    component: SmokeComponent,
  },
  {
    path: 'pic',
    component: PicComponent,
  },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ComponentsRoutingModule { }

export const routedComponents = [
    CommonUserComponent,
    IndexComponent,
    TrafficChartComponent,
    MapComponent,
    GatewayComponent,
    SmokeComponent,
    GatewayDetailComponent,
    PicComponent
];


