@Component({
    selector: 'ngx-map',
    templateUrl: "./map.html",
    styleUrls:['./map.scss']
  })
export class MapComponent implements OnInit {
    private projectID
    private src
    private smokes:Array<any>
    constructor(auth : AuthService, private http: Http){
        let url = "/api/sensor/v1?project="
        this.projectID = auth.getCurrentProjectID()
        url += this.projectID
        this.src = "/upload-dir/p" + this.projectID 
        this.http.get(url).toPromise().then((res)=>{
            this.smokes = res.json()
            this.make()
        })
    }
    map: any 
    make(){
        this.map = new BMap.Map("allmap")
        console.log("run")
        if(this.smokes.length){
            let p1 = this.smokes[0]
            // var point = new BMap.Point(116.404, 39.915);
            var point = new BMap.Point(parseFloat(p1["longi"]), parseFloat(p1["lati"]));
            this.map.centerAndZoom(point, 15);
            var bounds = this.map.getBounds();
            var sw = bounds.getSouthWest();
            var ne = bounds.getNorthEast();
            for(var i=0;i < this.smokes.length;i++){
                let p = this.smokes[i]
                let x = parseFloat(p['longi'])
                let y = parseFloat(p['lati'])
                var point = new BMap.Point(x, y);
                this.addMarker(point); 
            }
        }
        var top_left_control = new BMap.ScaleControl({anchor: BMAP_ANCHOR_TOP_LEFT});// 左上角，添加比例尺
	    var top_left_navigation = new BMap.NavigationControl();  //左上角，添加默认缩放平移控件
	    var top_right_navigation = new BMap.NavigationControl({anchor: BMAP_ANCHOR_TOP_RIGHT, type: BMAP_NAVIGATION_CONTROL_SMALL}); 
        this.map.enableScrollWheelZoom(); 
        this.map.addControl(top_left_control);        
		this.map.addControl(top_left_navigation);     
		this.map.addControl(top_right_navigation);   
    }
    change(event){
        console.log(event)
        if(event.tabTitle!="地图"){
        return
        }
        setTimeout(()=>{
            this.make()
        }, 500)
    }
    ngOnInit(){
        console.log("init");
        // this.map = new BMap.Map("allmap")
    }
    addMarker(point){
        var marker = new BMap.Marker(point);
        this.map.addOverlay(marker);
    }
}


