import {Component, OnInit} from '@angular/core'
import { AuthService } from '../../../auth/auth.service';



@Component({
    selector: 'ngx-pic',
    template:`
    <nb-card size="xxlarge">
    <nb-card-header>
        平面图
    </nb-card-header>

    <nb-card-body>
        
        <img *ngIf="src" alt="没有项目图" src="{{src}}" style="width:100%;">
        <h2 *ngIf="!src">没有项目图，如有需要，请联系管理员</h2>
    </nb-card-body>
</nb-card>
    `
})
export class PicComponent implements OnInit{
    private project
    private projectID
    private src
    constructor(auth : AuthService){
        auth.getProjectOb().subscribe((res)=>{
            this.project = res[0]
            this.src = "http://sensor.zhongyuniot.com/upload-dir/" + this.project['graph']
        })
    }
    ngOnInit(){

    }
}


