import { AfterViewInit, Component, OnDestroy } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import {Http, URLSearchParams} from '@angular/http'
import {ProjectService} from '../../../@core/data/projects.detail.service'
declare const echarts: any;
import {ConventULR} from "../../pages.component"
const points = [300, 520, 435, 530, 730, 620, 660, 860];

@Component({
  selector: 'ngx-traffic-chart',
  styleUrls: ['./index.scss'],
  template: `
  <nb-card size="small">
  <nb-card-header>
      <span>报表</span>

      <input type="month" placeholder="年-月" [(ngModel)]="selectedDate" (ngModelChange)="dateChanged()">
  </nb-card-header>
  <nb-card-body class="p-0">
      <div echarts [options]="option" class="echart"></div>
  </nb-card-body>
</nb-card>

  `,providers:[ProjectService]
})
export class TrafficChartComponent implements AfterViewInit, OnDestroy {

  type = 'month';
  selectedDate = "2017-12"
  types = ['week', 'month', 'year'];
  option: any = {};
  data: Array<any> = [];
  themeSubscription: any;
  url = ConventULR("/api/report/v1/")
  constructor(
      private theme: NbThemeService,
      private http: Http,
      private projectService: ProjectService
  ) {
    //   this.url += projectService.getUserProjectID()
      this.loadData()
  }

  dateChanged(){
      console.log(this.selectedDate)
      this.loadData()
  }

  ngAfterViewInit() {
      this.selectedDate = "2017-12"
    //   this.loadData()
  }
 loadData(){
    var url = this.url + "?project=1&start=0&end=1512834987000"
    this.http.get(url).toPromise().then(
        (res)=>{
           var data = res.json()
           for(var p in data){
               this.data.push({"value": data[p], "name": p})
           }
           this.config()
        }
    )
 }
 config(){
    this.themeSubscription = this.theme.getJsTheme().delay(1).subscribe(config => {
      const trafficTheme: any = config.variables.traffic;

      this.option = Object.assign({}, {
        title : {
            text: '报警统计',
            x:'center',
            textStyle: {
                color: trafficTheme.tooltipTextColor,
                fontWeight: trafficTheme.tooltipFontWeight,
                fontSize: 16,
            },
        },
        tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)",
            textStyle: {
                color: trafficTheme.tooltipTextColor,
                fontWeight: trafficTheme.tooltipFontWeight,
                fontSize: 16,
              },
            position: 'top',
            backgroundColor: trafficTheme.tooltipBg,
            borderColor: trafficTheme.tooltipBorderColor,
            borderWidth: 3,
            extraCssText: trafficTheme.tooltipExtraCss,
        },
        legend: {
            orient: 'vertical',
            left: 'left',
            data: ['火警','低电压','故障','静音','失联'],
            textStyle: {
                color: trafficTheme.tooltipTextColor,
                fontWeight: trafficTheme.tooltipFontWeight,
                fontSize: 16,
            },
        },
        series : [
            {
                name: '报警统计',
                type: 'pie',
                radius : '55%',
                center: ['50%', '60%'],
                data:this.data,
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }
        ]
      });
    });
 
 }
  ngOnDestroy() {

  }
}



