import { Component } from '@angular/core';

@Component({
  selector: 'ngx-tree',
  template: `
<div class="row">
  <div class="col-md-6">
    <nb-card>
      <nb-card-header>网关：0000123</nb-card-header>
      <nb-card-body>
       
        <ul>
            <li *ngFor="let i of tree" (click)="select(i)"  >烟感器：{{i}}</li>
          
        </ul>


      </nb-card-body>
    </nb-card>
  </div>
  <div class="col-md-6">
    <nb-card>
      <nb-card-header>{{head}}</nb-card-header>
      <nb-card-body>
       烟感器的状态信息{{selected}}
      </nb-card-body>
    </nb-card>
  </div>
</div>
  `,
  styles:[
      `
          li {
            cursor: pointer;
          }

      `
  ]
})
export class GatewayDetailComponent {
  head = "请选择要查看的烟感器"
  selected 
  select (id){
     this.head = "烟感器:"+id
     this.selected = id
     console.log(this.head)
  }
  tree = [
      1,2,3,4,5,6,7,8,9
  ]
    
  

}


