import { Component, OnInit,OnDestroy } from '@angular/core'
import { RemoteDataSource} from "../../../@core/utils/remoteDataSource"
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { CompleterService, CompleterData } from 'ng2-completer';
import { Http } from '@angular/http';
import {ConventULR} from "../../pages.component"
import { AuthService } from '../../../auth/auth.service';
@Component({
    selector: 'common-user-index',
    template: `
    <nb-card>
    <nb-card-header>
      {{head}}
      <button (click)="onExport()"  class="btn btn-primary btn-icon btn-tn right-btn" type="button">
      导出<i  class="fa fa-external-link"></i>
    </button>
    </nb-card-header>
  
    <nb-card-body>
      <ng2-smart-table [settings]="settings" [source]="source" (deleteConfirm)="onDeleteConfirm($event)">
      </ng2-smart-table>
    </nb-card-body>
  </nb-card>
    `,
    styles: [
        `
        nb-card {
            transform: translate3d(0, 0, 0);
          }
          .right-btn {
              float:right
          }
          .add-text{
            font-size: 14px;
          }
        `
    ]


})
export class SmokeComponent implements OnInit, OnDestroy{
    head = "烟感器管理"
    // type_filter = [
    //     { value: '已报警', title: '已报警' },
    //     { value: '火警', title: '火警' },
    //     { value: '低电压', title: '低电压' },
    //     { value: '故障', title: '故障' },
    //     { value: '未知', title: '未知' },
    //     { value: '失联', title: '失联' },
    // ]
    status_filter = [
        { value: '正常', title: '正常' },
        { value: '已报警', title: '已报警' },
        { value: '火警', title: '火警' },
        { value: '低电压', title: '低电压' },
        { value: '故障', title: '故障' },
        { value: '未知', title: '未知' },
        { value: '失联', title: '失联' },
    ]
    settings = {
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            inputClass: "add-input"
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
            confirmDelete: true,
        },
        columns: {
            eui: {
                title: '型号',
                type: 'string'
            },
            type: {
                title: '类型',
                type: 'string',
                // filter: {
                //     type: 'list',
                //     config: {
                //         selectText: '选择类别',
                //         list: this.type_filter,
                //     },
                // },
            },
            phone:{
                title:"联系人电话",
                type: "string"
            },
            location: {
                title: '位置',
                type: 'string'
            },
            installTime: {
                title: '安装时间',
                type: 'string',
                valuePrepareFunction: (date) => { 
                    var d = new Date(date)
                    return d.toLocaleString('zu-ZA').slice(0,10);
                }
            },
            guarantee: {
                title: '保修情况',
                type: 'string'
            },
            status: {
                title: '状态',
                type: 'string',
                filter: {
                    type: 'list',
                    config: {
                        selectText: '所有类别',
                        list: this.status_filter,
                    },
                },
            },
        },
       actions:false 
    }
    source: RemoteDataSource;
    url= ConventULR("/api/sensor/v1?project=")
    constructor(private service: SmartTableService,
         private http: Http,
         auth : AuthService
        ) {
        let project = auth.getCurrentProjectID()
        this.url += project
        const data = this.service.getData();
        this.source = new RemoteDataSource(http, { endPoint: this.url ,filterFieldKey: '#field#'});
    }
    ngOnInit(){
   }
   onDeleteConfirm(event): void {
    if (window.confirm('确定删除吗？')) {
        event.confirm.resolve();
    } else {
        event.confirm.reject();
    }
   }
   onExport(){
       let u = this.url + "&_limit=1000000"
       this.http.get(this.url).toPromise().then(
           (res)=>{
               let objects = res.json()
               let csv = "data:text/csv;charset=utf-8,%EF%BB%BF";

               let csvContent = ""
               var keys = ["型号", "类型", "联系人电话",
                "位置", "报修情况", "状态"]
               csvContent += keys.join(",") + "\n";

               objects.forEach(function(o){
                  let row = [
                      o.eui, o.model, o.phone,
                      o.location, o.guarantee, o.status

                  ]
                  csvContent += row + "\n"; // add carriage return
               }); 
               let   encodedUri = csv + encodeURI(csvContent);
               var link = document.createElement("a");
                link.setAttribute("href", encodedUri);
                link.setAttribute("download", "my_data.csv");
                document.body.appendChild(link); // Required for FF

                link.click();
           }
       ).catch()
   }

  ngOnDestroy() {
  }
}


