import { Component, OnInit, OnDestroy } from '@angular/core'
import { trigger, style, animate, transition } from '@angular/animations';
import { NbThemeService } from '@nebular/theme';
import { ProjectService } from '../../../@core/data/projects.detail.service'
import { ConventULR } from "../../pages.component"
import { Http, URLSearchParams } from '@angular/http'
import { ToasterService } from 'angular2-toaster';
import { Toast,ToasterConfig } from 'angular2-toaster';

declare var laydate

@Component({
  selector: 'common-user-index',
  templateUrl: './index.html',
  styleUrls: ['./index.scss'],
  animations: [
    trigger(
      'enterAnimation', [
        transition(':enter', [
          style({ transform: 'translateX(100%)', opacity: 0 }),
          animate('500ms', style({ transform: 'translateX(0)', opacity: 1 }))
        ]),
        transition(':leave', [
          style({ transform: 'translateX(0)', opacity: 1 }),
          animate('500ms', style({ transform: 'translateX(100%)', opacity: 0 }))
        ])
      ]
    )
  ],
  providers: [ProjectService]
})
export class IndexComponent implements OnInit, OnDestroy {
  show = true;
  selectedDate = "2017-12"
  currentTheme: string;
  themeSubscription: any;
  buttons: Array<any>
  alarm_url = ConventULR("/api/sensor/v1/alarm")
  url = ConventULR("/api/sensor/v1/")
  params: URLSearchParams
  alarm_data: Array<any>
  filter_alarm_data: Array<any>
  show_type = ""
  fire_count = 0
  lowp_count = 0
  err_count = 0
  alarm_err_count = 0    // 上次请求警报的个数，一旦发生变更，要重新拉取监控
  dataRange: any
  projectID
  historyData: Array<any>
  historyPage = 1
  historyPageCount = 10
  historyTotalCount = 0
  historyTotalPage:number = 0
  lastStart = 0
  lastEnd = 0
  totalCount=  0
  config:ToasterConfig
  constructor(private themeService: NbThemeService,
    private projectService: ProjectService,
    private http: Http,
    private toasterService: ToasterService,
  ) {
    this.themeSubscription = this.themeService.getJsTheme().subscribe(theme => {
      this.currentTheme = theme.name;
      this.projectID = this.projectService.getUserProjectID()
      this.params = new URLSearchParams()
      this.params.set("project", this.projectID.toString())
      this.params.set("_limit", "10000000")
      this.http.get(this.url,
        { search: this.params }
      ).toPromise().then(
        (res) => {
          this.buttons = res.json()
          this.count()
          this.watch()
        }
        )
    });
  }
  load() {
    // 改写成加载全部数据
    this.http.get(this.url,
      { search: this.params }
    ).toPromise().then(
      (res) => {
        this.buttons = res.json()
        this.count()
      }
      )

  }
  executeAsync(func) {
    setTimeout(func, 0);
  }
  watch() {
    var toa = this.toasterService
    this.http.get(this.alarm_url,
      { search: this.params }
    ).toPromise().then(
      (res) => {
        this.alarm_data = res.json()
        var count = 0
        var item
        for (var i of this.alarm_data) {
          if (i['status'] == "火警") {
            count++
            item = i
            this.play()
          }
        }
        this.filter()
        if (count != 0) {
          toa.pop("error", "发现火情!!!", "位置:" + item['location'])
          this.load()
        }
        if (count != this.alarm_err_count) {
          this.alarm_err_count = count
          this.load()
          const toast: Toast =  {
              type:"warning",
              title: "警告⚠️发现火情!!!(请确认)",
              body:"位置:" + item['location'],
              timeout:0,
              showCloseButton: true,
          }
          toa.popAsync(toast) 
        }
      }
      )
  }
  filter() {
    // console.log(1)
    if (this.show_type == "") {
      this.filter_alarm_data = this.alarm_data
    }
    else {
      this.filter_alarm_data = []
      for (let i of this.alarm_data) {
        if (i['status'] == this.show_type) {
          this.filter_alarm_data.push(i)
        }
      }
    }
    // console.log(this.filter_alarm_data)
  }
  count() {
    if (this.buttons){
    this.totalCount = this.buttons.length
    }
    this.fire_count = 0
    this.lowp_count = 0
    this.err_count = 0
    for (var i of this.buttons) {
      if (i['status'] == "火警" || i['status'] == "已报警") {
        this.fire_count++
      }
      else if (i['status'] == "低电压") {
        this.lowp_count++
      }
      else if (i['status'] == "正常") {
      }
      else {
        this.err_count++
      }
    }
  }
  id: any
  ngOnInit() {
    this.id = setInterval(
      () => {
        this.watch()
      }, 7000
    )

  }
  change(event) {
    console.log(event)
    if(event.tabTitle!="历史警报"){
      return
    }
    let mythis = this
    setTimeout(()=>{
      console.log("run")
      this.search(1, 0, 100000000000000000)
      laydate.render({
        elem: '#test1'
        , type: 'datetime'
        , range: true,
        done: function (value, date, endDate) {
          console.log(value); //`得到日期生成的值，如：2017`-08-18
          console.log(date); //得到日期时间对象：{year: 2017, month: 8, date: 18, hours: 0, minutes: 0, seconds: 0}
          console.log(endDate); //得结束的日期时间对象，开启范围选择（range: true）才会返回。对象成员同上。
          var d = new Date(`${date.year}-${date.month}-${date.date} ${date.hours}: ${date.minutes}:${date.seconds}`)
          date = endDate
          var sd = new Date(`${date.year}-${date.month}-${date.date} ${date.hours}: ${date.minutes}:${date.seconds}`)
          mythis.lastStart = d.getTime()
          mythis.lastEnd = sd.getTime()
          mythis.search(1, d.getTime(), sd.getTime())

        }
      });
    }, 
    
    200)

  }
  close(obj) {
    obj['status'] = "已报警"
    this.http.put(this.url, obj).toPromise().then((res) => {
      this.toasterService.popAsync("success", "已标记")
    })
      .catch((e) => {
        this.toasterService.popAsync("error", "标记失败")

      })
  }

  search(page, start, end) {
    var url = "/api/op/v1?project=" + this.projectID +`&_page=${page}&_limit=${this.historyPageCount}&start=${start}&end=${end}`
    var p = this.http.get(url).toPromise().then(
      (res)=>{
        let s = res.headers.get("x-total-count")
        this.historyTotalCount = parseInt(s)
        this.historyTotalPage =  Math.floor((this.historyTotalCount + this.historyPageCount-1) / this.historyPageCount)
        this.historyData = res.json()
      }
    )
  }
  save(i){
    console.log(i)
    let url = "/api/op/v1"
    this.http.put(url, i).toPromise()
    .then((res)=>{
      this.toasterService.pop("success","保存成功")
      this.search(1, 0, 100000000000000000)
    })
    .catch((e)=>{
      this.toasterService.pop("error","保存失败")
    })
  }
  onPageChange(action){
    if(action==1)
    {
      this.historyPage += 1
    }
    else{
      this.historyPage -= 1
    }
    this.search(this.historyPage, this.lastStart, this.lastEnd)
  }

  ngOnDestroy() {
    this.themeSubscription.unsubscribe();
    if (this.id) {
      clearInterval(this.id);
    }
  }
  warningInfo(){
    this.projectService.getObject(this.projectID).then((p)=>{
      if (!p['phone']){
        p['phone'] = "对不起，改项目没有录入紧急联系人电话"
      }
      this.toasterService.popAsync("info", "紧急联系人电话：" + p['phone'])
    })
    
  }
  onBlur(i) {
    if (!i.location) {
      i.location = "未知"
    }
    this.toasterService.popAsync("info", `设备:${i.eui}
                                          状态：${i.status} 
                                          地址：${i.location}`)
  }
  play() {
    var audio = document.createElement("audio");
    audio.src = "/assets/2206.mp3";
    audio.play();
  }
}


